/* Scss Document */

.fck_detail table.tplCaption td {
    line-height: 0;
    padding: 0;
}

.fck_detail table.tplCaption td.caption,
.fck_detail table.tplCaption td.Image,
.fck_detail table.tplCaption td p.Normal,
.fck_detail table.tplCaption td .Image,
.fck_detail .tplCaption .Image {
    font: 400 12px arial;
    color: #333;
    background: #f5f5f5;
    padding: 10px;
    margin: 0;
}

#container_tab_live {
    float: left;
    margin: 8px 0 20px;
}

.fck_detail p.Image {
    margin: 0;
    padding: 5px;
}

.fck_detail {
    color: #333;

    td {
        padding: 2px;
    }
}

/*Thăm dò*/
#boxthamdoykien {
    margin: 0 0 10px;
    display: block;
}

.txt_main_category {
    float: left;
    margin: 0 0 0 10px;
    display: inline;
    font: 700 14px/30px arial;
    color: #9f224e;
}

#boxthamdoykien .title_box_category {
    margin: 0;
}

.title_box_category {
    min-height: 30px;

    span {
        color: $vne_color;
        font: 700 14px/20px arial;
        padding: 6px 0;

        &:after {
            content: '';
            width: 100%;
            height: 2px;
            position: absolute;
            top: 0;
            left: 0;
            background: #999;
            z-index: 9;
        }
    }

    &:after {
        content: '';
        width: 100%;
        height: 2px;
        background: #dadada;
        position: absolute;
        top: 0;
        left: 0;
    }
}

#boxthamdoykien .content_box_category {
    background: #f5f5f5;
}

.block_padding_10 {
    padding: 10px;
}

#boxthamdoykien .title_thamdo {
    font: 400 14px arial;
}

#boxthamdoykien .title_thamdo {
    padding: 0 0 10px;
    border-bottom: 1px solid #dadada;
    margin-bottom: 10px;
    text-align: left;
}

#boxthamdoykien .tb_servey {
    text-align: left;
}

#boxthamdoykien .tb_servey td,
#boxthamdoykien .tb_servey td {
    padding: 0 0 9px;
}

#boxthamdoykien .vote_2col td {
    width: 48%;
    vertical-align: top;
    margin: 0 2% 0 0;
}

.tb_servey.vote_2col .w20 {
    width: 8%;
}

.tb_servey .w20 {
    float: left;
}

#boxthamdoykien .input_servey {
    float: left;
    margin: 0 5px 0 0;
}

.fck_detail .tb_servey.vote_2col td .w130 {
    width: 90%;
    margin: 0;
    float: left;
}

/*kết thúc Thăm dò*/

/*btn_quantam*/
.social_share .btn_quantam {
    display: block;
    height: 20px;
    width: 75px;
    background: #eee;
    float: left;
    margin: 0;
    line-height: 20px;
    font-size: 11px;
    text-align: center;
    color: #656565;
}

.social_share .btn_quantam .ic {
    color: #9f224e;
    font-size: 11px;
    vertical-align: middle;
    line-height: 16px;
}

.social_share .number_count {
    height: 20px;
    padding: 0 0 0 7px;
    float: left;
    margin: 0 0 0 4px;
    position: relative;
}

/*End btn_quantam*/

/*Insert video*/
.block_tinlienquan_temp {
    background: #eee;
    border: 1px solid #d6d6d6;
    margin-bottom: 20px;
}

.item_tinlq_temp {
    width: 50%;
    float: left;
    padding: 10px;

    .thumb {
        height: auto;
    }

    .title_news {
        margin: 10px 0;
        width: 100%;
        float: left;

        a {
            font-weight: normal;
            line-height: 18px;
        }
    }
}

/*End Insert video*/

/*Quảng cáo trong bài viết*/
.box_quangcao {
    width: 96%;
    padding: 10px 2% !important;
    float: left;
    background: #eee;
    border-top: 2px solid #dadada;

}

.mg_bottom_20 {
    margin-bottom: 15px !important;
}

.box_quangcao.list_news .thumb {
    width: 150px !important;
    height: auto !important;
    margin-bottom: 5px !important;
}

.list_news .thumb {
    float: left;
    margin: 0 10px 0 0;
    position: relative;
}

.box_quangcao .title_news {
    width: auto;
    float: none;
    margin: 0;
    line-height: normal !important;

    a {
        color: #333;
        font: 700 14px arial;

        &:hover {
            color: #004f8b;
        }
    }
}

