/*block_related*/
article.block_related {
    .thumb {
        width: 100% !important;
        height: 0 !important;

        img {
            width: 100%;
        }
    }

    .title_news {
        margin-bottom: 10px !important;
    }
}

/*End block_related*/

/*list_related_items*/
.list_related_items {
    border: none;
    padding: 10px;
    background: #f4f4f4;
    margin-bottom: 10px;

    li {
        border: none !important;
        margin: 0 !important;
        padding: 0 0 10px 10px !important;

        a {
            font: 700 12px arial;
            color: #666 !important;

            &:hover {
                color: #666 !important;
            }
        }
    }
}

/*End list_related_items*/

/*slide*/

.fck_detail .list_slide {
    width: 100%;
    float: left;
    position: relative;
    margin-bottom: 10px;
}

.btn_control_tv_slider {
    height: 28px;
    width: 28px;
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    z-index: 9;
    top: 40%;
    text-align: center;
    line-height: 28px;
    cursor: pointer;
}

.btn_control_tv_slider .ic {
    color: #000;
    vertical-align: middle;
}

.btn_control_tv_slider.prev_slider {
    left: 0;
}

.btn_control_tv_slider.next_slider {
    right: 0;
}

.fck_detail .btn_control_tv_slider {
    top: 40%;
}

/*end slide*/

/*insert interview*/
.content_vptt {
    background: #eee;
    font: 700 12px arial;
    width: 100%;
    float: left;
}

.fck_detail #phongvantructuyen {
